.dashboard {
  flex-direction: column;
  text-align: center;

  @include sm {
    form {
      padding: 0 1rem;
      flex-direction: column;
      justify-content: center;
    }
  }

  .sentHearts {
    h1 {
      width: 100%;
    }
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-bottom: 3rem;
  }

  .results {
    align-items: flex-start;
  }

  .noresults {
    background-color: #f0f0f0;
    color: $primary;
    border-radius: 1rem;
    margin: 3rem 0;
    padding: 1rem;
  }

  .form {
    button {
      align-self: center;
    }

    &.expand {
      form {
        flex-direction: row;
        justify-content: center;

        input,
        select {
          margin-right: 1rem;
        }

        button {
          margin-top: 0;
        }
      }

      @include sm {
        form {
          padding: 0 1rem;
          flex-direction: column;
          justify-content: center;
        }
      }

      .results {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
      }
    }

    button {
      font-size: 20px;
      color: $text;
      font-family: "Quicksand", sans-serif;
      border: none;
      border-radius: 12px;
      outline: 0;
      background-color: $primary;
      color: $background;
      width: max-content;
      padding: 0.5rem 2rem;
      cursor: pointer;
      transition: all 0.25s ease-in-out;

      svg {
        width: 2rem;
        height: 2rem;
      }

      margin: 1rem 0 0;

      .loader {
        animation: spin 2s linear infinite;

        @keyframes spin {
          0% {
            transform: rotate(0deg);
          }
          100% {
            transform: rotate(360deg);
          }
        }
      }

      &:disabled {
        opacity: 0.5;
        background-color: grey;
      }

      &:hover {
        transform: scale(1.05) translateY(-4px);
        box-shadow: 0px 10px 20px rgba($color: $text, $alpha: 0.5);
      }
      &:active {
        transform: scale(0.95) translateY(0);
      }
    }
  }
}
