.card {
  width: 45%;
  margin-top: 3rem;
  margin-bottom: 3rem;
  height: 17rem;

  .card-container {
    // height: 16rem;
    background-color: $background-alt;
    flex-shrink: 0;
    border-radius: 24px;
    padding: 1rem 2rem;
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    height: 17rem;

    .title {
      margin-bottom: 00.5rem;
      cursor: pointer;
      overflow: hidden;
      text-align: left;
      max-width: 100%;
    }

    .card-body {
      text-align: left;
      color: #c7747e;
      font-size: 1.5rem;
      p {
        margin: 0.5rem 0;
      }
      margin-bottom: 3rem;
    }

    button {
      position: absolute;

      padding: 0.5rem 1rem;
      margin: 0.5rem 0;
      bottom: -2rem;
      right: -2rem;
      border: none;
      border-radius: 12px;
      font-size: 20px;
      color: $text;
      font-family: "Quicksand", sans-serif;
      background-color: $primary;
      color: $background;
      width: max-content;
      padding: 0.5rem 2rem;
      cursor: pointer;
      transition: all 0.25s ease-in-out;

      svg {
        width: 3rem;
        height: 3rem;
      }

      &:hover {
        transform: scale(1.05) translateY(-4px);
        box-shadow: 0px 10px 20px rgba($color: $text, $alpha: 0.5);
      }
      &:active {
        transform: scale(0.95) translateY(0);
      }
    }
  }

  @include sm {
    width: 80%;
    height: inherit;

    .card-container {
      height: inherit;
      .title {
        height: 100%;
        overflow: visible;
      }
    }
  }
}

.ReactModal__Overlay.ReactModal__Overlay--after-open {
  background-color: rgba(#000000, 0.75) !important;
}

.modal {
  background-color: $background-alt;
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 2rem 2rem 3rem;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  max-height: 25rem;

  box-shadow: 0 0 20px $primary;
  outline: 0;
  max-width: 400px;

  .error {
    color: $primary;
  }

  .card-title {
    color: #a0616a;
  }

  .card-body {
    color: #c7747e;
  }

  .actions {
    display: flex;
    float: right;
    align-self: flex-end;
    margin-top: 2rem;
    flex-wrap: wrap;
  }

  button {
    outline: 0;
    margin: 0.5rem;
    border: none;
    border-radius: 12px;
    font-size: 20px;
    color: $text;
    font-family: "Quicksand", sans-serif;
    background-color: $primary;
    color: $background;
    width: max-content;
    padding: 0.5rem 2rem;
    cursor: pointer;
    transition: all 0.25s ease-in-out;

    &.gray {
      background-color: #808080;
    }

    .loader {
      animation: spin 2s linear infinite;

      @keyframes spin {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }
    }

    svg {
      width: 2rem;
      height: 2rem;
    }

    &:disabled {
      opacity: 0.5;
      background-color: grey;
    }

    &:hover {
      transform: scale(1.05) translateY(-4px);
      box-shadow: 0px 10px 20px rgba($color: $text, $alpha: 0.5);
    }
    &:active {
      transform: scale(0.95) translateY(0);
    }
  }
}
